body{
    overflow: none;
}

.TeamContainer {

    width: 100vw;
    height: 92vh;
    position: absolute;
    top: 8vh;
    padding: 1.5rem 5rem;
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
}

.MemberContainer:first-child {
    grid-row: span 2;
}

@media screen and (max-width: 960px){

    .TeamContainer{
        width: 100vw;
        position: absolute;
        padding: 1.5rem 0;
        top: 8vh;
        display: grid;
        gap: 1.5rem;
        grid-template-columns: 1fr;
    }

    .MemberContainer {
        width: 80vw;
        min-height: 85vh;
        max-height: 85vh;
    }
}