* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.hide {
  display: none;
}

.HomeContainer {
  position: fixed;
  top: 8vh;
  width: 100%;
  height: 92vh;
}

.LogoCover {

  margin: auto;
  height: 60vh;
  width: 100vw;
  top: 8vh;
  background-image: url('../public/SOAR\ 16X9.jpeg');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.HomeContainer h1 {
  padding-top: 0;
}

@media screen and (max-width: 960px){
  .LogoCover {
    height: 30vh;
  }

  .HomeContainer h1 {
    font-size: 1.5rem;
  }
}