.MemberContainer {
    min-height: 30vh;
    width: 25vw;
    background-color: rgba(255, 255, 255);
    box-shadow: 0px 10px 10px rgba(0,0,0, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0 30px;
}

.ProfilePicture {
    min-height: 20vh;
    max-height: 20vh;
    width: 20vh;
    border-radius: 100%;
    margin-top: 30px;
    background-size: cover;
    background-repeat: no-repeat;
}

.MemberTextContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100%;
    width: 100%;
}

.MemberTextContainer h1 {
    font-size: 1.5rem;
    padding: 20px 0 10px 0;
}

.MemberTextContainer h5 {
    text-align: center;
    color: rgba(0,0,0, 0.5);
    font-size: 0.9rem;
    font-weight: 100;
}

.MemberTextContainer p {
    margin-top: 20px;
    width: 100%;
    height: auto;
    max-height: 40vh;
    font-size: 0.8rem;
    overflow-y: scroll;
}

.ContactIcons {
    position: sticky;
    font-size: 1.8rem;
    height: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 10px;
}

.ContactIcons a {
    color: #000;
    transition: all ease-in-out 0.2s;
}

.ContactIcons a:hover {
    transition: all ease-in-out 0.2s;
    color: #E69249;
}

.ContactIcons i {
    padding: 0 20px;
}

@media screen and (max-width: 960px) {
    .MemberTextContainer p {
        height: 35vh;
    }
}